<script>
  import { onMount, beforeUpdate, afterUpdate } from 'svelte';
  import { notifier } from '@beyonk/svelte-notifications';
  import { URL, handleError } from '../../../../constants';
  import { staffStore } from '../../../../stores.js';
  
  export let route;

  let staff = [];
  let copy = [];
  $: schoolname = '';
  $: editModal = false;
  let selected;

  staffStore.useLocalStorage();

  staffStore.subscribe(items => {
    staff = items;
    copy = items;
  });


  onMount(() => {
    fetch(`${URL}/staff/${route.params.id}`, {
      headers: {
        'Authorization': localStorage.getItem('auth-token')
      }
    })
    .then(res => res.json())
    .then(data => {

      if(data.status === 'success') {
        staffStore.set(data.data);
        notifier.success(data.message);
      } else {
        staffStore.set([]);
        handleError(data.message);
      }
      localStorage.setItem('auth-token', data.token)
    })
    .catch(error => console.log(error));
  })


  function handleChange(e) {
    switch(selected) {
      case 'tenured':
        const tenured = copy.filter(el => {
          return el.employment_type.toLowerCase() === "tenured" 	
        });
        staff = [...tenured];
        break;
      case 'tenured-teaching':
        const tenured_teaching = copy.filter(el => {
          return el.employment_type.toLowerCase() === "tenured" && 	el.staff_type.toLowerCase() === "teaching"
        });
        staff = [...tenured_teaching];
        break;
      case 'non-tenured':
        const non_tenured = copy.filter(el => {
          return el.employment_type.toLowerCase() === "non-tenured" 	
        });
        staff = [...non_tenured];
        break;
      // case 'non-tenured-teaching':
      //   const non_tenured_teaching = copy.filter(el => {
      //     return el.employment_type.toLowerCase() === "non-tenured" && el.staff_type.toLowerCase() === "non-teaching"
      //   });
      //   staff = [...non_tenured_teaching];
      //   break;
      default:
        staff = [...copy];
        break;
    }
  }

  function openEditModal() {
    document.querySelector('body').classList.add('modal-open');
    editModal = true;
  }

  function handleDelete(e) {

  }

  function closeEditModal() {
    document.querySelector('body').classList.remove('modal-open');
    editModal = false;
  }
  

</script>

