              <option value="all" selected="">All Staff</option>
              <option value="tenured">Tenure Staff</option>
              <option value="tenured-teaching">Tenure Teaching Staff</option>
              <option value="non-tenured">Non-Tenured Staff</option>
              <!-- <option value="non-tenured-teaching">Non-Tenured Teaching Staff</option> -->
            </select>
            <div class="table-responsive">
